var ttt = 0;
//高德SDK
import amap from '@/common/SDK/amap-wx.js';
export default {
	data() {
		return {
			kword:"",
			showHeader: true,
			afterHeaderOpacity: 1, //不透明度
			headerPosition: 'fixed',
			headerTop: null,
			statusTop: null,
			nVueTitle: null,
			city: '北京',
			currentSwiper: 0,
			// 轮播图片
			swiperList: [{
					id: 1,
					src: 'url1',
					img: '/static/img/1.jpg'
				},
				{
					id: 2,
					src: 'url2',
					img: '/static/img/2.jpg'
				},
				{
					id: 3,
					src: 'url3',
					img: '/static/img/3.jpg'
				}
			],
			// 分类菜单
			categoryList: [{
					id: 1,
					name: '数码',
					img: '/static/img/category/1.png'
				},
				{
					id: 2,
					name: '家电',
					img: '/static/img/category/2.png'
				},
				{
					id: 3,
					name: '饰品',
					img: '/static/img/category/3.png'
				},
				{
					id: 4,
					name: '日用',
					img: '/static/img/category/4.png'
				},
				{
					id: 5,
					name: '健康',
					img: '/static/img/category/5.png'
				},
				{
					id: 6,
					name: '运动',
					img: '/static/img/category/6.png'
				},
				{
					id: 7,
					name: '书籍',
					img: '/static/img/category/7.png'
				},
				{
					id: 8,
					name: '服装',
					img: '/static/img/category/8.png'
				},
				{
					id: 9,
					name: '书籍',
					img: '/static/img/category/7.png'
				},
				{
					id: 10,
					name: '服装',
					img: '/static/img/category/8.png'
				}
			],
			Promotion: [],
			//猜你喜欢列表
			productList: [],
