		};
	},
	onPageScroll(e) {
		//兼容iOS端下拉时顶部漂移
		this.headerPosition = e.scrollTop >= 0 ? "fixed" : "absolute";
		this.headerTop = e.scrollTop >= 0 ? null : 0;
		this.statusTop = e.scrollTop >= 0 ? null : -this.statusHeight + 'px';
	},
	//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
	onPullDownRefresh() {
		this.load();
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	//上拉加载，需要自己在page.json文件中配置"onReachBottomDistance"
	onReachBottom() {
		uni.showToast({
			title: '触发上拉加载'
		});
		let len = this.productList.length;
		if (len >= 1000) {
			this.loadingText = '到底了';
			return false;
		}
		this.load();
	},
	onLoad() {








		this.amapPlugin = new amap.AMapWX({
			//高德地图KEY，随时失效，请务必替换为自己的KEY，参考：http://ask.dcloud.net.cn/article/35070
			key: '7c235a9ac4e25e482614c6b8eac6fd8e'
		});
		//定位地址
		this.amapPlugin.getRegeo({
			success: data => {
				this.city = data[0].regeocodeData.addressComponent.city.replace(/市/g, ''); //把"市"去掉






			}
		});
		//开启定时器
		this.Timer();
		this.load()//请求商品的数据
		//加载活动专区
		this.loadPromotion();
	},
	methods: {
		load() {
			uni.request({
				url: 'http://120.79.19.253:10086/Index', //仅为示例，并非真实接口地址。
				data: {
					type: "new",
				},
				success: (res) => {
					console.log("$$$$$$$$$$$$$$$$$$$$$$$$", res.data.data);
					this.text = 'request success';
					var data = res.data.data;
					var datalist = [{}];
					var imagelistsplit = [];
					for (var item of data) {
						var imagereg = /;/;
						if (imagereg.test(item.pimages)) {
							var j = item.pimages.split(";");
							imagelistsplit.push(j[0]);
						} else {
							imagelistsplit.push(item.pimages)
						}
					}
					for (var item in data) {
						datalist[item] = {
							"goods_id": data[item].pid,
							"img": imagelistsplit[item],
							"name": data[item].pname,
							"price": data[item].price,
							"slogan": data[item].watched
						};

					}
					console.log(datalist, "%%%%%%%%%%%%%%%%%%%%%%%%%%%");
					if(this.productList.length!==0)
					{
						this.productList = [...this.productList, ...datalist]
					}else{
						this.productList = [ ...datalist]
					}
					
				}
			});
		},
		//加载Promotion 并设定倒计时,,实际应用中应该是ajax加载此数据。
		loadPromotion() {
			let cutTime = new Date();
			let yy = cutTime.getFullYear(),
				mm = cutTime.getMonth() + 1,
				dd = cutTime.getDate();
			let tmpcountdown = yy + '/' + mm + '/' + dd + ' 23:59:59';
			let tmpPromotion = [{
					title: '整点秒杀',
					ad: '整天秒杀专区',
					img: '/static/img/s1.jpg',
					countdown: false
				},
				{
					title: '限时抢购',
					ad: '每天23点上线',
					img: '/static/img/s2.jpg',
					countdown: tmpcountdown
				} //countdown为目标时间，程序会获取当前时间倒数
			];
			//检查倒计时
			for (let i = 0; i < tmpPromotion.length; i++) {
				let row = tmpPromotion[i];
				if (row.countdown) {
					let h = '00',
						m = '00',
						s = '00';
					let currentTime = new Date();
					let cutoffTime = new Date(tmpcountdown);
					if (!(currentTime >= cutoffTime)) {
						let countTime = parseInt(
							(cutoffTime.getTime() - currentTime.getTime()) / 1000
						);
						h = parseInt(countTime / 3600);
						m = parseInt((countTime % 3600) / 60);
						s = countTime % 60;
						h = h < 10 ? '0' + h : h;
						m = m < 10 ? '0' + m : m;
						s = s < 10 ? '0' + s : s;
					}
					tmpPromotion[i].countdown = {
						h: h,
						m: m,
						s: s
					};
				}
			}
			this.Promotion = tmpPromotion;
		},
		//定时器
		Timer() {
			setInterval(() => {
				if (this.Promotion.length > 0) {
					for (let i = 0; i < this.Promotion.length; i++) {
						let row = this.Promotion[i];
						if (row.countdown) {
							if (
								!(
									row.countdown.h == 0 &&
									row.countdown.m == 0 &&
									row.countdown.s == 0
								)
							) {
								if (row.countdown.s > 0) {
									row.countdown.s--;
									row.countdown.s =
										row.countdown.s < 10 ?
										'0' + row.countdown.s :
										row.countdown.s;
								} else if (row.countdown.m > 0) {
									row.countdown.m--;
									row.countdown.m =
										row.countdown.m < 10 ?
										'0' + row.countdown.m :
										row.countdown.m;
									row.countdown.s = 59;
								} else if (row.countdown.h > 0) {
									row.countdown.h--;
									row.countdown.h =
										row.countdown.h < 10 ?
										'0' + row.countdown.h :
										row.countdown.h;
									row.countdown.m = 59;
									row.countdown.s = 59;
								}
								this.Promotion[i].countdown = row.countdown;
							}
						}
					}
				}
			}, 1000);
		},
		//消息列表
		toMsg() {
			uni.navigateTo({
